.c-gift-card-template {
  margin: 5rem auto 8rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  width: 80%;
  max-width: 100rem;

  &__title {
    width: 80%;
    text-align: center;
    margin: 0 auto 2rem;
  }

  &__image {
    display: block;
    width: 30%;
    margin: 2rem auto 2rem;
    max-width: 11rem;
  }

  &__value{
    margin: 2rem 0;
  }

  &__redeem {
    margin-bottom: 1.5rem;
  }

  .o-btn {
    max-width: 20rem;
    margin: 0 auto;
  }

  @include t-media-query($s-medium-up) {
    margin-top: 15rem;

    &__title {
      margin-bottom: 3rem;
    }

    &__image {
      margin: 3rem auto;
    }

    &__code {
      font-size: 1.2rem;;
    }

    &__redeem {
      font-size: 1.2rem;
      margin-bottom: 2rem;
    }
  }
}
